import React from 'react'

/**
 * Badge pour afficher le statut d'une commande
 */
function OrderStatusBadge({ status }) {
  const statusConfig = {
    NEW: { label: 'Nouvelle', className: 'bg-blue-100 text-blue-700' },
    AGGREGATED: { label: 'Agrge', className: 'bg-purple-100 text-purple-700' },
    SUPPLIER_ORDERED: { label: 'Commande fournisseur', className: 'bg-indigo-100 text-indigo-700' },
    PREPARATION: { label: 'En prparation', className: 'bg-orange-100 text-orange-700' },
    LIVRAISON: { label: 'En livraison', className: 'bg-amber-100 text-amber-700' },
    LIVREE: { label: 'Livre', className: 'bg-teal-100 text-teal-700' },
    ANNULEE: { label: 'Annule', className: 'bg-red-100 text-red-700' },
    brouillon: { label: 'Brouillon', className: 'bg-gray-100 text-gray-700' },
    envoye: { label: 'Envoye', className: 'bg-blue-100 text-blue-700' },
    confirme: { label: 'Confirme', className: 'bg-yellow-100 text-yellow-700' },
    en_prparation: { label: 'En prparation', className: 'bg-orange-100 text-orange-700' },
    prte: { label: 'Prte', className: 'bg-green-100 text-green-700' },
    livre: { label: 'Livre', className: 'bg-teal-100 text-teal-700' },
    annule: { label: 'Annule', className: 'bg-red-100 text-red-700' },
  }

  const config = statusConfig[status] || { label: status || 'Inconnu', className: 'bg-gray-100 text-gray-700' }

  return (
    <span className={`px-3 py-1 rounded-full text-xs font-semibold ${config.className}`}>
      {config.label}
    </span>
  )
}

export default OrderStatusBadge
