import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

// Enregistrer le Service Worker pour PWA (seulement en production ou si activ)
if ('serviceWorker' in navigator) {
  // Ne pas enregistrer le SW en dveloppement (react-scripts ne le sert pas correctement)
  // Il sera actif automatiquement en production aprs build
  if (process.env.NODE_ENV === 'production' || process.env.REACT_APP_ENABLE_SW === 'true') {
    window.addEventListener('load', () => {
      // D'abord, dsinscrire tous les Service Workers existants pour viter les conflits
      navigator.serviceWorker.getRegistrations().then((registrations) => {
        registrations.forEach((registration) => {
          registration.unregister().then(() => {
            console.log(' Ancien Service Worker dsinscrit');
          });
        });
      }).then(() => {
        // Attendre un peu avant de renregistrer
        return new Promise(resolve => setTimeout(resolve, 100));
      }).then(() => {
        // Enregistrer le nouveau Service Worker
        return navigator.serviceWorker
          .register('/sw.js', { scope: '/' })
          .then((registration) => {
            console.log(' Service Worker enregistr avec succs:', registration.scope);
            
            // Vrifier les mises  jour priodiquement
            setInterval(() => {
              registration.update();
            }, 60000); // Vrifier chaque minute
            
            return registration;
          });
      }).catch((error) => {
        console.log(' chec de l\'enregistrement du Service Worker:', error.message);
        // Ne pas bloquer l'application si le SW ne peut pas s'enregistrer
      });
    });
  } else {
    // En dveloppement, dsinscrire les SW existants pour viter les erreurs
    window.addEventListener('load', () => {
      navigator.serviceWorker.getRegistrations().then((registrations) => {
        registrations.forEach((registration) => {
          registration.unregister();
        });
        if (registrations.length > 0) {
          console.log('  Service Worker dsactiv en mode dveloppement');
        }
      });
    });
  }
}

