import React, { useState, useEffect } from 'react'
import { Package, AlertCircle, CheckCircle2, AlertTriangle, Minus, Plus } from 'lucide-react'
import { useAuth } from '../../contexts/AuthContext'
import { useStore } from '../../contexts/StoreContext'
import { usePermission } from '../../hooks/usePermission'
import { RESOURCES, ACTIONS } from '../../constants/permissions'
import ProtectedRoute from '../../components/common/ProtectedRoute'
import OrderStatusBadge from '../../components/OrderStatusBadge'
import Modal from '../../components/common/Modal'
import ConfirmDialog from '../../components/common/ConfirmDialog'
import Toast from '../../components/common/Toast'
import { useToast } from '../../hooks/useToast'
import { getOrdersToPrepare, getOrdersInPreparation, prepareOrder, markOrderReady } from '../../api/orders'
import { getStock } from '../../api/stocks'
import { format } from 'date-fns'

/**
 * Page de prparation des commandes - MAGASIN
 * 
 * Logique mtier :
 * - Affiche les commandes confirmes ou en prparation
 * - Permet de prparer une commande en ajustant les quantits relles
 * - Gre les carts entre command et prpar
 * - Dcrmente le stock uniquement lors de la prparation
 */
function Preparation() {
  const { user } = useAuth()
  const { activeStore } = useStore()
  const { canPrepare, canMarkReady } = usePermission(RESOURCES.ORDERS)
  const { toast, showToast, hideToast, showSuccess, showError } = useToast()
  
  const [orders, setOrders] = useState([])
  const [loading, setLoading] = useState(true)
  const [selectedOrder, setSelectedOrder] = useState(null)
  const [preparationModalOpen, setPreparationModalOpen] = useState(false)
  const [preparationItems, setPreparationItems] = useState([])
  const [confirmDialog, setConfirmDialog] = useState({ isOpen: false, orderId: null })

  useEffect(() => {
    if (activeStore?.id) {
      loadOrders()
    }
  }, [activeStore])

  const loadOrders = async () => {
    try {
      setLoading(true)
      // Rcuprer les commandes  prparer et en prparation
      const [toPrepare, inPreparation] = await Promise.all([
        getOrdersToPrepare(activeStore.id),
        getOrdersInPreparation(activeStore.id),
      ])

      let allOrders = [
        ...(toPrepare?.orders ?? []),
        ...(inPreparation?.orders ?? []),
      ]
      if (activeStore?.id) {
        allOrders = allOrders.filter((o) => o.shopId === activeStore.id)
      }
      allOrders.sort((a, b) => {
        if (a.status === 'SUPPLIER_ORDERED' && b.status === 'PREPARATION') return -1
        if (a.status === 'PREPARATION' && b.status === 'SUPPLIER_ORDERED') return 1
        return new Date(b.createdAt) - new Date(a.createdAt)
      })

      setOrders(allOrders)
    } catch (error) {
      showError('Erreur lors du chargement des commandes')
      console.error(error)
    } finally {
      setLoading(false)
    }
  }

  const handleStartPreparation = (order) => {
    // Initialiser les quantits prpares avec les quantits commandes
    const items = order.items.map((item) => ({
      ...item,
      preparedQuantity: item.orderedQuantity || item.quantity,
      gap: 0,
      gapReason: '',
      availableStock: 0, // Sera charg
    }))

    // Charger les stocks disponibles pour chaque produit
    Promise.all(
      items.map(async (item) => {
        try {
          const stockData = await getStock(activeStore.id, item.productId)
          return {
            ...item,
            availableStock: stockData?.availableQuantity ?? stockData?.stock ?? 0,
          }
        } catch (error) {
          return { ...item, availableStock: 0 }
        }
      })
    ).then((itemsWithStock) => {
      setPreparationItems(itemsWithStock)
      setSelectedOrder(order)
      setPreparationModalOpen(true)
    })
  }

  const handleUpdatePreparedQuantity = (itemIndex, newQuantity) => {
    setPreparationItems((prev) => {
      const updated = [...prev]
      const item = updated[itemIndex]
      
      if (!item) return prev

      const orderedQuantity = item.orderedQuantity || item.quantity
      const preparedQuantity = Math.max(0, newQuantity)
      const gap = orderedQuantity - preparedQuantity

      updated[itemIndex] = {
        ...item,
        preparedQuantity,
        gap,
      }

      return updated
    })
  }

  const handleAddGapReason = (itemIndex, reason) => {
    setPreparationItems((prev) => {
      const updated = [...prev]
      if (updated[itemIndex]) {
        updated[itemIndex].gapReason = reason
      }
      return updated
    })
  }

  const handleConfirmPreparation = async () => {
    if (!selectedOrder) return

    try {
      // Prparer les donnes pour l'API
      const preparedItems = preparationItems.map((item) => ({
        productId: item.productId,
        unit: item.unit,
        preparedQuantity: item.preparedQuantity || 0,
        gapReason: item.gapReason || null,
      }))

      // Appeler l'API pour prparer la commande
      await prepareOrder(selectedOrder.id, preparedItems, user.id)

      showSuccess('Commande prpare avec succs')
      setPreparationModalOpen(false)
      setSelectedOrder(null)
      setPreparationItems([])
      loadOrders()
    } catch (error) {
      showError('Erreur lors de la prparation de la commande')
      console.error(error)
    }
  }

  const handleMarkReady = async (orderId) => {
    try {
      await markOrderReady(orderId)
      showSuccess('Commande marque comme prte')
      loadOrders()
    } catch (error) {
      showError('Erreur lors de la mise  jour de la commande')
      console.error(error)
    }
    setConfirmDialog({ isOpen: false, orderId: null })
  }

  const ordersToPrepare = orders.filter((o) => o.status === 'confirme')
  const ordersInPreparation = orders.filter((o) => o.status === 'en_prparation')

  return (
    <ProtectedRoute requiredResource={RESOURCES.ORDERS} requiredAction={ACTIONS.PREPARE}>
      <div className="space-y-6 animate-fade-in">
        {/* Header */}
        <div>
          <h1 className="text-3xl font-bold text-gray-900 mb-2">
            Prparation des Commandes
          </h1>
          {activeStore && (
            <p className="text-gray-600">
              {activeStore.name} - Commandes  prparer et en cours de prparation
            </p>
          )}
        </div>

        {/* Alertes */}
        {ordersToPrepare.length > 0 && (
          <div className="card bg-gradient-to-r from-orange-50 to-red-50 border-l-4 border-orange-500">
            <div className="flex items-center space-x-4">
              <div className="p-3 bg-orange-100 rounded-xl">
                <AlertCircle className="h-6 w-6 text-orange-600" />
              </div>
              <div className="flex-1">
                <h3 className="text-lg font-bold text-gray-900 mb-1">
                  {ordersToPrepare.length} commande(s)  prparer
                </h3>
                <p className="text-sm text-gray-600">
                  Commencez la prparation de ces commandes
                </p>
              </div>
            </div>
          </div>
        )}

        {/* Commandes  prparer */}
        {ordersToPrepare.length > 0 && (
          <div className="card">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-xl font-bold text-gray-900 flex items-center space-x-2">
                <Package className="h-5 w-5 text-primary-600" />
                <span>Commandes  prparer ({ordersToPrepare.length})</span>
              </h2>
            </div>

            <div className="space-y-4">
              {ordersToPrepare.map((order) => (
                <div
                  key={order.id}
                  className="p-5 bg-gradient-to-r from-white to-gray-50 rounded-xl border border-gray-200 hover:shadow-lg transition-all"
                >
                  <div className="flex items-start justify-between mb-4">
                    <div className="flex-1">
                      <div className="flex items-center space-x-3 mb-2">
                        <span className="font-bold text-gray-900 text-lg">
                          {order.orderNumber}
                        </span>
                        <OrderStatusBadge status={order.status} />
                        <span className="text-sm text-gray-600">
                          {format(new Date(order.createdAt), 'dd MMM yyyy  HH:mm')}
                        </span>
                      </div>
                      <p className="text-sm text-gray-600 mb-2">
                        <span className="font-medium">Livraison prvue :</span>{' '}
                        {format(new Date(order.deliveryDate), 'dd MMMM yyyy')}
                      </p>
                      <p className="text-lg font-bold text-primary-600">
                        {order.totalTTC?.toFixed(2) || order.total?.toFixed(2)}  TTC
                      </p>
                    </div>
                    {canPrepare && (
                      <button
                        onClick={() => handleStartPreparation(order)}
                        className="btn btn-primary flex items-center space-x-2"
                      >
                        <Package className="h-4 w-4" />
                        <span>Prparer</span>
                      </button>
                    )}
                  </div>

                  {/* Articles de la commande */}
                  <div className="mt-4 pt-4 border-t border-gray-200">
                    <p className="text-sm font-semibold text-gray-700 mb-2">
                      Articles ({order.items?.length || 0})
                    </p>
                    <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
                      {order.items?.slice(0, 4).map((item, index) => (
                        <div
                          key={index}
                          className="p-2 bg-gray-50 rounded-lg text-sm"
                        >
                          <p className="font-medium text-gray-900">
                            {item.product?.name || 'Produit'}
                          </p>
                          <p className="text-gray-600">
                            {item.orderedQuantity || item.quantity} {item.unit}
                          </p>
                        </div>
                      ))}
                      {order.items?.length > 4 && (
                        <div className="p-2 bg-gray-50 rounded-lg text-sm flex items-center justify-center">
                          <span className="text-gray-600">
                            +{order.items.length - 4} autre(s)
                          </span>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Commandes en prparation */}
        {ordersInPreparation.length > 0 && (
          <div className="card">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-xl font-bold text-gray-900 flex items-center space-x-2">
                <Package className="h-5 w-5 text-primary-600" />
                <span>En cours de prparation ({ordersInPreparation.length})</span>
              </h2>
            </div>

            <div className="space-y-4">
              {ordersInPreparation.map((order) => (
                <div
                  key={order.id}
                  className="p-5 bg-gradient-to-r from-blue-50 to-primary-50 rounded-xl border border-primary-200"
                >
                  <div className="flex items-start justify-between mb-4">
                    <div className="flex-1">
                      <div className="flex items-center space-x-3 mb-2">
                        <span className="font-bold text-gray-900 text-lg">
                          {order.orderNumber}
                        </span>
                        <OrderStatusBadge status={order.status} />
                      </div>
                      {order.items?.some((item) => item.gap && item.gap !== 0) && (
                        <div className="mt-2 p-2 bg-yellow-100 border border-yellow-300 rounded-lg">
                          <div className="flex items-center space-x-2 text-yellow-800 text-sm">
                            <AlertTriangle className="h-4 w-4" />
                            <span className="font-medium">
                              carts dtects entre command et prpar
                            </span>
                          </div>
                        </div>
                      )}
                    </div>
                    {canMarkReady && (
                      <button
                        onClick={() =>
                          setConfirmDialog({ isOpen: true, orderId: order.id })
                        }
                        className="btn btn-success flex items-center space-x-2"
                      >
                        <CheckCircle2 className="h-4 w-4" />
                        <span>Marquer prte</span>
                      </button>
                    )}
                  </div>

                  {/* Articles avec carts */}
                  {order.items && order.items.length > 0 && (
                    <div className="mt-4 pt-4 border-t border-gray-200">
                      <div className="space-y-2">
                        {order.items.map((item, index) => {
                          const orderedQty = item.orderedQuantity || item.quantity
                          const preparedQty = item.preparedQuantity
                          const gap = item.gap || 0

                          return (
                            <div
                              key={index}
                              className={`p-3 rounded-lg ${
                                gap !== 0
                                  ? 'bg-yellow-50 border border-yellow-200'
                                  : 'bg-gray-50'
                              }`}
                            >
                              <div className="flex items-center justify-between">
                                <div className="flex-1">
                                  <p className="font-medium text-gray-900">
                                    {item.product?.name || 'Produit'}
                                  </p>
                                  <div className="flex items-center space-x-4 mt-1 text-sm">
                                    <span className="text-gray-600">
                                      Command : <span className="font-medium">{orderedQty}</span> {item.unit}
                                    </span>
                                    <span className="text-gray-600">
                                      Prpar : <span className="font-medium">{preparedQty || orderedQty}</span> {item.unit}
                                    </span>
                                    {gap !== 0 && (
                                      <span className="text-yellow-700 font-medium">
                                        cart : {gap > 0 ? '-' : '+'}{Math.abs(gap)} {item.unit}
                                      </span>
                                    )}
                                  </div>
                                  {item.gapReason && (
                                    <p className="text-xs text-gray-600 mt-1 italic">
                                      Raison : {item.gapReason}
                                    </p>
                                  )}
                                </div>
                              </div>
                            </div>
                          )
                        })}
                      </div>
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Message si aucune commande */}
        {orders.length === 0 && !loading && (
          <div className="card text-center py-12">
            <Package className="h-16 w-16 text-gray-400 mx-auto mb-4" />
            <h2 className="text-xl font-semibold text-gray-900 mb-2">
              Aucune commande  prparer
            </h2>
            <p className="text-gray-600">
              Toutes les commandes sont prpares ou aucune commande confirme
            </p>
          </div>
        )}

        {/* Modal de prparation */}
        <Modal
          isOpen={preparationModalOpen}
          onClose={() => {
            setPreparationModalOpen(false)
            setSelectedOrder(null)
            setPreparationItems([])
          }}
          title={`Prparer la commande ${selectedOrder?.orderNumber}`}
          size="xl"
        >
          {selectedOrder && (
            <div className="space-y-6">
              <div className="bg-gray-50 p-4 rounded-lg">
                <p className="text-sm text-gray-600 mb-2">
                  <span className="font-medium">Client :</span> {selectedOrder.clientId}
                </p>
                <p className="text-sm text-gray-600">
                  <span className="font-medium">Livraison prvue :</span>{' '}
                  {format(new Date(selectedOrder.deliveryDate), 'dd MMMM yyyy')}
                </p>
              </div>

              <div className="space-y-4">
                <h3 className="font-semibold text-gray-900">
                  Ajuster les quantits prpares
                </h3>
                {preparationItems.map((item, index) => {
                  const orderedQty = item.orderedQuantity || item.quantity
                  const preparedQty = item.preparedQuantity || 0
                  const gap = orderedQty - preparedQty
                  const hasGap = gap !== 0

                  return (
                    <div
                      key={index}
                      className={`p-4 rounded-xl border ${
                        hasGap
                          ? 'bg-yellow-50 border-yellow-200'
                          : 'bg-white border-gray-200'
                      }`}
                    >
                      <div className="flex items-start justify-between mb-3">
                        <div className="flex-1">
                          <p className="font-semibold text-gray-900 text-lg">
                            {item.product?.name || 'Produit'}
                          </p>
                          <p className="text-sm text-gray-600">
                            Stock disponible : <span className="font-medium">{item.availableStock || 0}</span> {item.unit}
                          </p>
                        </div>
                      </div>

                      <div className="grid grid-cols-2 gap-4 mb-3">
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">
                            Quantit commande
                          </label>
                          <div className="p-3 bg-gray-50 rounded-lg text-center">
                            <span className="text-lg font-bold text-gray-900">
                              {orderedQty} {item.unit}
                            </span>
                          </div>
                        </div>
                        <div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">
                            Quantit prpare
                          </label>
                          <div className="flex items-center space-x-2">
                            <button
                              onClick={() =>
                                handleUpdatePreparedQuantity(
                                  index,
                                  preparedQty - 1
                                )
                              }
                              className="p-2 border border-gray-300 rounded-lg hover:bg-gray-100"
                              disabled={preparedQty <= 0}
                            >
                              <Minus className="h-4 w-4" />
                            </button>
                            <input
                              type="number"
                              min="0"
                              value={preparedQty}
                              onChange={(e) =>
                                handleUpdatePreparedQuantity(
                                  index,
                                  parseInt(e.target.value) || 0
                                )
                              }
                              className="flex-1 p-3 border border-gray-300 rounded-lg text-center font-bold text-lg"
                            />
                            <button
                              onClick={() =>
                                handleUpdatePreparedQuantity(
                                  index,
                                  preparedQty + 1
                                )
                              }
                              className="p-2 border border-gray-300 rounded-lg hover:bg-gray-100"
                            >
                              <Plus className="h-4 w-4" />
                            </button>
                          </div>
                        </div>
                      </div>

                      {hasGap && (
                        <div className="mt-3 p-3 bg-yellow-100 border border-yellow-300 rounded-lg">
                          <div className="flex items-center space-x-2 mb-2">
                            <AlertTriangle className="h-4 w-4 text-yellow-600" />
                            <span className="text-sm font-medium text-yellow-800">
                              cart : {gap > 0 ? '-' : '+'}{Math.abs(gap)} {item.unit}
                            </span>
                          </div>
                          <label className="block text-sm font-medium text-gray-700 mb-1">
                            Raison de l'cart (optionnel)
                          </label>
                          <textarea
                            value={item.gapReason || ''}
                            onChange={(e) => handleAddGapReason(index, e.target.value)}
                            className="w-full p-2 border border-gray-300 rounded-lg text-sm"
                            rows="2"
                            placeholder="Ex: Stock insuffisant, produit endommag..."
                          />
                        </div>
                      )}
                    </div>
                  )
                })}
              </div>

              <div className="flex items-center justify-end space-x-3 pt-4 border-t border-gray-200">
                <button
                  onClick={() => {
                    setPreparationModalOpen(false)
                    setSelectedOrder(null)
                    setPreparationItems([])
                  }}
                  className="btn btn-secondary"
                >
                  Annuler
                </button>
                <button
                  onClick={handleConfirmPreparation}
                  className="btn btn-primary flex items-center space-x-2"
                >
                  <CheckCircle2 className="h-4 w-4" />
                  <span>Confirmer la prparation</span>
                </button>
              </div>
            </div>
          )}
        </Modal>

        {/* Dialog de confirmation */}
        <ConfirmDialog
          isOpen={confirmDialog.isOpen}
          onClose={() => setConfirmDialog({ isOpen: false, orderId: null })}
          onConfirm={() => handleMarkReady(confirmDialog.orderId)}
          title="Marquer la commande comme prte"
          message="tes-vous sr de vouloir marquer cette commande comme prte ? Elle sera alors disponible pour la livraison."
          confirmText="Marquer prte"
          type="info"
        />

        {/* Toast */}
        <Toast {...toast} onClose={hideToast} />
      </div>
    </ProtectedRoute>
  )
}

export default Preparation
